/* Float four columns side by side */
.columnSmall {
  float: left;
  /* width: 25%; */
  /* padding: 0 10px; */
}




.column {
  float: left;
  width: 25%;
  padding: 0 10px;
}

.columnColors {
  width: 25%;
  padding: 0 10px;
}

.columnColor {
  float: left;
  width: 25%;
  padding: 0px;
}

/* Remove extra left and right margins, due to padding in columns */
.row {margin: 0 -5px;}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns - one column layout (vertical) on small screens */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 600px) {
  .columnColor {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}
  

